<template>
  <div>{{underlyingValue}}
  <chart ref='chart' :chart-data="datacollection"></chart>
    <button v-on:click="addLabel">add {{type}}</button>&nbsp;
    <button v-on:click="storeCurrentData">save  {{type}}</button>
    <ul>
      <li v-for="k in history" :key="k" v-on:click="applyStoredData(k)">{{k}} <span v-on:click="removeStoredItems(k)">Remove</span></li>
    </ul>
  </div>
</template>
<script>

var requesting = typeof(requesting) !="undefined" ? requesting : false;
var timer = typeof(timer) !="undefined" ? timer : null;
var timer_start = typeof(timer_start) !="undefined" ? timer_start : true;
    
import Chart from "../Chart.js";
export default {
  props: ["type","interval"],
  components: {
    Chart
  },
  data() {
    return {
      datacollection: null,
      underlyingValue:null,
      history:[]
    };
  },
  mounted() {
    this.fillData();
    if(timer_start==true){
    timer = setInterval(this.addLabel,this.interval);
    }

      this.getStoredItems();
  },
  methods: {
    getStoredItems(){
      const items = { ...localStorage };
      this.history =  Object.keys(items);
    },
    removeStoredItems(key){
      
		localStorage.removeItem(key)
    },
    applyStoredData(k){
      
      var data = this.getWithExpiry(k);
if(data){
      this.applyData(JSON.parse(data),this);
      timer_start = false;
      clearInterval(timer);
  // return false;
}
    },
    storeCurrentData(){
      var data = this.getWithExpiry('data');
      var d = new Date();
      var time = d.getDate()+'_'+d.getHours()+'_'+d.getMinutes()+'_'+d.getSeconds();
      if(data){
				this.setWithExpiry("saved_data_"+time, JSON.stringify(data), 60*60*60*60);
      }
      this.getStoredItems();
    },
 setWithExpiry(key, value, ttl) {
	const now = new Date()

	// `item` is an object which contains the original value
	// as well as the time when it's supposed to expire
	const item = {
		value: value,
		expiry: now.getTime() + ttl,
	}
	localStorage.setItem(key, JSON.stringify(item))
},
getWithExpiry(key) {
	const itemStr = localStorage.getItem(key)
	// if the item doesn't exist, return null
	if (!itemStr) {
		return null
	}
	const item = JSON.parse(itemStr)
	const now = new Date()
	// compare the expiry time of the item with the current time
	if (now.getTime() > item.expiry) {
		// If the item is expired, delete the item from storage
		// and return null
		localStorage.removeItem(key)
		return null;
	}
	return item.value
},
    getData(resolve){

var data = this.getWithExpiry('data');
if(data){
  resolve(JSON.parse(data));
  return false;
}

var url = '';
    //  url = 'http://localhost:8080/option-chain-indices.json';
     url = 'https://www.nseindia.com/api/option-chain-indices?symbol=NIFTY';
var th = this;

if(requesting==false){
var fetcha = fetch(url,{
  credentials: 'omit',
  headers: {
    
  mode: 'no-cors', // 'cors' by default
  "Access-Control-Allow-Origin":"*",
  Origin: "nseindia.com"

  }}).then((r)=>{
    if(r.status==200){
      return r.json();
    }else{
      return false;
    }}).then(function(r){
      if(r){
				th.setWithExpiry("data", JSON.stringify(r), 5000);

requesting = false;
        }

    });

try{
  resolve(fetcha);
}catch(e){
  console.log('Error');
}
requesting = true;
}
    },
    applyData(r,th){
       if(typeof(r)=='undefined' || r==false){
         return false;
       }
    var type = th.type;

    let strikes = [];
     let ce_s = [];
     let pe_s = [];
     let pcr = [];
     r = typeof(r)=='object' ? r : JSON.parse(r);
if(r && r.records && r.records.underlyingValue){
       th.underlyingValue = r.records.underlyingValue;
       }
    var da =r.filtered.data;
  // console.log(r.filtered.data)
    da.forEach(function(d){
      // console.log(d);

    // console.log('asdssss',type);
  strikes.push(d.strikePrice);
  ce_s.push(d.CE[type]);
  pe_s.push(d.PE[type]);
  var pcr_v = d.PE[type]/d.CE[type];
  pcr_v = pcr_v > 10 ? 10 : ((pcr_v < 0.00) ? 0 : pcr_v);
  pcr_v = pcr_v.toFixed(2);
  pcr.push(pcr_v);
    });

    var ce_min = Math.min(...ce_s);
    var ce_max = Math.max(...ce_s);
    var pe_min = Math.min(...pe_s);
    var pe_max = Math.max(...pe_s);


var stike_min = Math.min(ce_min,pe_min);
var stike_max = Math.max(ce_max,pe_max);

stike_min,stike_max

// console.log(strikes,ce_s,pe_s);
 th.datacollection = {
  ...th.datacollection,
  lables:strikes,
  datasets:[
          // {
          //   backgroundColor: "#f87979",
          //   data: strikes
          // },
          {
            label:"Call "+type,
            backgroundColor: "#e93f38",
            data: ce_s
          },
          {
            label:"Put "+type,
            backgroundColor: "#3f7d0c",
            data: pe_s
          },
          {
            label:"Ratio "+type,
            backgroundColor: "#00f",
            data: pcr
          }
        ]
 }
    },
    addLabel() {
timer_start = true;

    timer = setInterval(this.addLabel,this.interval);
    
    var th = this;
     new Promise(this.getData).then((r)=>{
th.applyData(r,th);
th.getStoredItems();
// console.log({...this.datacollection});
      // this.$refs.chart.render() // Not Chart.render()
})

      // this.datacollection.labels.push(13); //Chart is not updating....
      // //Chart.render(); NOT working
      // this.$refs.chart.render() // Not Chart.render()
    // this.datacollection.labels.concat(Math.floor(Math.random() * 10));
      // this.datacollection.datasets[0].data.concat(Math.floor(Math.random() * 10));

//       this.datacollection = {
//   ...this.datacollection,
//   labels: this.datacollection.labels.concat(Math.floor((Math.random() * 50)+50)), // avoid to mutate data
//   datasets: this.datacollection.datasets[0].data[1]
// }

// console.log({...this.datacollection})
// var d = this.datacollection.datasets[0].data;
// d.push(Math.floor((Math.random() * 50)+50));


// var d2 = this.datacollection.datasets[0].data;
// d2.push(Math.floor((Math.random() * 50)+50));

// var l = this.datacollection.labels;
// l.push(Math.floor((Math.random() * 50)+50));



    },
    fillData() {

var strike_start = 27800; 
var strike_end = 37800; 

var strikes = [];

for(let i = strike_start; i<strike_end;i+=100){
  // console.log('asd',i);
  strikes.push(i);
}
      this.datacollection = {
        labels: strikes,
        datasets: [
          {
            backgroundColor: "#f87979",
            data: [0,10,60]
          },
          {
            backgroundColor: "#e93f38",
            data: [0,20,50]
          },
          {
            backgroundColor: "#3f7d0c",
            data: [0,30,-40]
          },
          {
            backgroundColor: "#00f",
            data: [0]
          }
        ]
      };
    }
  },
  watch: {
    data: function() {
      this._chart.destroy();
      //this.renderChart(this.data, this.options);
      this.fillData();
    }
  }
};
</script>
